import React from 'react';
import '../../src/styles/registerStyles.css';
import { Button, Form, Input, message } from 'antd';
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { showLoading, hideLoading } from '../redux/features/alertSlice';

const Register = () => {
  const navigate = useNavigate();
const dispatch = useDispatch();
//Form Handler
  const onFinishHandler = async(values) => {
try {
  dispatch(showLoading())
  const res = await axios.post('/api/v1/user/register', values)
  dispatch(hideLoading())
  if(res.data.success){
    message.success('Register Successfully!')
    navigate('/login');
  } else {
    message.error(res.data.message);
  }
} catch (error) {
  dispatch(hideLoading())
  console.log(error);
  message.error('Something went wrong');
}
  }
  return (
    <>
    {/* <div className='form-contaiber'>
    <h1>Register Form</h1>
     <Form
      onFinish={onFinishHandler}
      layout="vertical"
      initialValues={{ remember: true }}
    >
      <Form.Item
        label="Name"
        name="name"
        rules={[{ required: true, message: 'Please input your name!' }]}
      >
        <Input />
      </Form.Item>

      <Form.Item
        label="Password"
        name="password"
        rules={[{ required: true, message: 'Please input your password!' }]}
      >
        <Input.Password />
      </Form.Item>

      <Form.Item>
        <Button type="primary" htmlType="submit">
          Submit
        </Button>
      </Form.Item>
    </Form>
    </div> */}
    <div className='form-contaiber'>
      {/* <Form layout='vertical' onFinish={onFinishHandler} className='card p-4 register-form' > */}
      <Form layout='vertical' onFinish={onFinishHandler} className='register-form' >
      <h3 className='text-center'>Register Form</h3>
        <Form.Item label='Name' name='name'>
          <Input type='name' required />
        </Form.Item>
        <Form.Item label='Email' name='email'>
          <Input type='email' required />
        </Form.Item>
        <Form.Item label='Password' name='password'>
          <Input type='password' required />
        </Form.Item>
        <Link to='/login' className=' text-white'>Already a user? Login here</Link>
        <Button className='btn btn-primary mt-2 bb' htmlType="submit">Register</Button>
      </Form>
    </div>
    </>
  )
}

export default Register
