// export default Layout;

import React from "react";
import "../styles/layoutStyles.css";
import { UserMenu, AdminMenu } from "../Data/sidebarData";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { message, Badge, Avatar } from "antd";
// import { Avatar, Badge, Space } from 'antd';
import { useDispatch } from "react-redux";
import { clearUser } from "../redux/features/userSlice";
import { logout } from "../redux/features/authSlice";

const Layout = ({ children }) => {
  const location = useLocation();
  const navigate = useNavigate();
  const { user } = useSelector((state) => state.user);
  const dispatch = useDispatch();

  const logoutHandler = () => {
    localStorage.clear();
    dispatch(clearUser()); // Clear user data from Redux store
    dispatch(logout());
    message.success("Logout Successfully");
    navigate("/login");
  };

  // SidebarMenu rendering
  const SidebarMenu = user?.isAdmin ? AdminMenu : UserMenu;
  return (
    <div className="main">
      <div className="layout">
        <div className="sidebar">
          <div className="logo">
            <h6>MED-APP</h6>
            <hr />
          </div>
          <div className="menu">
            {SidebarMenu.map((menu) => {
              const isctive = location.pathname === menu.path;
              return (
                <div
                  className={`menu-item ${isctive && "active"}`}
                  key={menu.path}
                >
                  <i className={menu.icon}></i>
                  <Link to={menu.path}>{menu.name}</Link>
                </div>
              );
            })}
            <div className="menu-item" onClick={logoutHandler}>
              <i className="fa-solid fa-right-from-bracket"></i>
              <Link to="/login">Logout</Link>
            </div>
          </div>
        </div>
        <div className="content">
          <div className="header">
            <div className="header-content">
            <Badge count={user && user?.notification.length}>
              {/* <Badge count={user?.notification?.length}> */}
                <i className="fa-sharp fa-solid fa-bell"></i>
              </Badge>
              {user ? (
                <Link to="/profile">{user?.name}</Link>
              ) : (
                <p>Loading user data...</p>
              )}
            </div>
          </div>
          <div className="body">{children}</div>
        </div>
      </div>
    </div>
  );
};

export default Layout;

